.shoppingCart {
  margin: 100px 0;
}

.basket__form {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  padding: 10px;
}

.basket__form-wrapper {
  grid-gap: 20px;
  display: grid;
  gap: 20px;
  grid-template-columns: 42% 1fr;
  margin-bottom: 24px;
}

@media screen and (max-width: 1170px) {
  .basket__form-wrapper {
    grid-template-columns: 100%;
  }
}

.basket__totalprice {
  display: flex;
  justify-content: center;
  align-items: center;
}

.basket__submit-btn {
  margin-left: 10px;
  background-color: #f9b300;
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.75;
  outline: 0;
  padding: 8px 40px;
  text-align: center;
}

.basket__submit-btn:hover {
  opacity: 0.7;
}
